.about{
    width: 100%;
    margin-top: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    height: 50%;
}

.about .left{
    text-align: center;
    margin:auto;
    padding: 1rem;
    max-width: 350px; 
}

.about .left .h1 {
    margin: 1.2rem 0;
    font-size: 2rem;
}

.about .left p{
    margin: 1.2rem 0;
    font-size: 1.2rem;
    line-height: 1.1rem;
}

.about .right {
    max-width: 700px;
}

.right .image-container {
    display: grid;
    grid-template-columns: repeat(12,1fr);
    position: relative;
    align-items: center;
    text-align: center;
}

.right .img {
    max-width: 90%;
    border: 1px solid #333;

}

.right .top {
    grid-column: 1 / span 8;
    grid-row: 1;
    padding-top: 20%;
    z-index: 6;

}

.right .bottom {
    grid-column: 4 / -1;
    grid-row: 1;
    z-index: 5;

}